<template>
  <div>
    <div class="h-16 shadow-md z-50 flex justify-center items-center">
      <ul>
        <li :class="isHomeActive ? 'active' : ''">
          <router-link to="/">Home</router-link>
        </li>
        <li :class="isAboutActive ? 'active' : ''">
          <router-link to="/about">About</router-link>
        </li>
        <li :class="isSkillActive ? 'active' : ''">
          <router-link to="/skill">Skill</router-link>
        </li>
        <li :class="isProjectActive ? 'active' : ''">
          <router-link to="/project">Project</router-link>
        </li>
      </ul>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: "DefaultLayout",
  components: {},
  data() {
    return {
      isAboutActive: false,
      isHomeActive: false,
      isSkillActive: false,
      isProjectActive: false,
    };
  },
  created() {
    const { path } = this.$route;
    if (path) {
      switch (path) {
        case "/":
          this.isHomeActive = true;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = false;
          break;
        case "/about":
          this.isHomeActive = false;
          this.isAboutActive = true;
          this.isSkillActive = false;
          this.isProjectActive = false;
          break;
        case "/skill":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = true;
          this.isProjectActive = false;
          break;
        case "/project":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = true;
          break;
      }
    }
  },
  updated() {
    const { path } = this.$route;
    if (path) {
      switch (path) {
        case "/":
          this.isHomeActive = true;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = false;
          break;
        case "/about":
          this.isHomeActive = false;
          this.isAboutActive = true;
          this.isSkillActive = false;
          this.isProjectActive = false;
          break;
        case "/skill":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = true;
          this.isProjectActive = false;
          break;
        case "/project":
          this.isHomeActive = false;
          this.isAboutActive = false;
          this.isSkillActive = false;
          this.isProjectActive = true;
          break;
      }
    }
  },
  mounted() {},

  methods: {},
};
</script>

<style lang="css" scoped>
ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

li {
  float: left;
}

li a {
  display: block;
  text-align: center;
  padding: 8px 8px;
  text-decoration: none;
}

li a:hover {
  color: #fc6b03;
}

li.active::after {
  content: "";
  display: block;
  margin-right: 16px;
  margin-left: 16px;
  width: 84px;
  border-bottom: 2px solid #333;
}
</style>
