<template>
  <div class="main-wrapper">
    <div id="sec1" class="section-wrapper grid md:grid-cols-2 sm:grid-cols-1 gap-4">
      <div id="3dmain"></div>
      <div class="flex items-center sm:p-20">
        <ul>
          <li class="text-8xl font-semibold"><h2>Hi! I'm Tuan</h2></li>
          <li class="text-5xl">
            <h3>I'm a Developer. Nice to meet you</h3>
          </li>
          <li class="flex items-center">
            <img class="social-icon-fb" src="/images/ic_fb.png" />
            <img class="social-icon" src="/images/ic_git.png" />
          </li>
          <li>
            <button
              class="px-6 py-2 mt-8 rounded-full text-xl font-normal btn-about"
              @click="scrollToView()"
            >
              About me
            </button>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import * as THREE from "three";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader.js";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls.js";
export default {
  name: "HomePage",

  data() {
    return {
      currentPage: 1,
      delay: true,
      pageCount: 4,
      isMove: false,
    };
  },
  async mounted() {
    const container = document.getElementById("3dmain");
    let camera = new THREE.PerspectiveCamera(
      50,
      container.clientWidth / container.clientHeight,
      0.1,
      1000
    );
    camera.position.set(-111, 94, 98);

    let renderer = new THREE.WebGLRenderer({
      antialias: true,
      alpha: true,
    });
    renderer.setPixelRatio(devicePixelRatio);
    renderer.setSize(container.clientWidth, container.clientHeight);
    renderer.gammaOuput = true;
    renderer.gammaFactor = 2.2;
    let scene = new THREE.Scene();
    const loader = new GLTFLoader();
    let model;
    loader.load(
      "/assets/models/scene.gltf",
      (gltf) => {
        model = gltf.scene;
        scene.add(model);
      },
      (xhr) => {
        console.log(xhr);
      },
      (error) => console.log(error)
    );

    var light = new THREE.AmbientLight(0xffffff);
    scene.add(light);

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.autoRotate = true;
    controls.autoRotateSpeed = 0.5;
    container.appendChild(renderer.domElement);
    renderer.setAnimationLoop(() => {
      onWindowResize();
      controls.update();
      renderer.render(scene, camera);
    });
    function onWindowResize() {
      camera.aspect = container.clientWidth / container.clientHeight;

      camera.updateProjectionMatrix();

      renderer.setSize(container.clientWidth, container.clientHeight);
    }

    // handler cloud
    // eslint-disable-next-line no-undef
    TagCanvas.Start("myCanvas", "tags", {
      textColour: "#000",
      outlineColour: "#ffffff",
      reverse: true,
      depth: 0.8,
      textHeight: 20,
      pinchZoom: false,
      wheelZoom: false,
      maxSpeed: 0.02,
    });
  },

  methods: {
    scrollToView() {
      this.$router.push('/about') 
    }
  },
};
</script>

<style lang="css" scoped>
ul li {
  margin-top: 8px;
}
.btn-about {
  background-color: #fc6b03;
}

.btn-about:hover {
  background-color: #e28743;
}

.social-icon {
  width: 38px;
  height: 38px;
  padding: 4px;
}
.social-icon-fb {
  width: 42px;
  height: 42px;
  padding: 4px;
}
</style>
